.codigoImg1{
    width: 100vw;
    margin-top: 10px;
}

@media screen and (min-width: 1024px) {
    .codigoImg1{
        width: 20vw;
    }
}


.preview{
    display: block;
    padding: 0px 2rem;
    box-sizing: border-box;
    height: 100%;
    white-space: pre-wrap;
    max-height: 390px;
    overflow-y: auto;
    font-size: 14px;
    scrollbar-color: #bebcbc #141414;
    scrollbar-width: thin;
    background: #141414;
    color: white;
}
